const { registerBlockType } = wp.blocks;
const { PlainText, MediaUpload, InnerBlocks } = wp.editor;
const { Button } = wp.components;

registerBlockType("howmuch/test-block", {
	title: "Basic Example",
	icon: "smiley",
	category: "layout",
	attributes: {
		title: {
			type: "string"
		},
		subtitle: {
			type: "string"
		},
		imageUrl: {
			attribute: "src"
		}
	},
	edit: props => {
		function setValue(content) {
			props.setAttributes({ title: content });
		}
		function setSubtitleValue(content) {
			props.setAttributes({ subtitle: content });
		}
		function getImageButton(openEvent) {
			if (props.attributes.imageUrl) {
				return (
					<img src={props.attributes.imageUrl} onClick={openEvent} />
				);
			} else {
				return <Button onClick={openEvent}>Pick an image</Button>;
			}
		}
		return (
			<div>
				<MediaUpload
					onSelect={media => {
						props.setAttributes({
							imageUrl: media.url
						});
					}}
					type="image"
					value={props.attributes.imageID}
					render={({ open }) => getImageButton(open)}
				/>
				<PlainText
					className={props.className}
					value={props.attributes.title}
					onChange={setValue}
					placeholder="title"
				/>
				<PlainText
					className={props.className}
					value={props.attributes.subtitle}
					onChange={setSubtitleValue}
					placeholder="subtitle"
				/>
			</div>
		);
	},
	save: props => {
		function cardImage(src) {
			if (!src) return null;
			return <img src={src} />;
		}
		return (
			<div>
				{cardImage(props.attributes.imageUrl)}
				<h3>{props.attributes.title}</h3>
				<h4>{props.attributes.subtitle}</h4>
			</div>
		);
	}
});

// Wrapper

registerBlockType("howmuch/my-wrapper", {
	title: "Basic Wrapper",
	icon: "smiley",
	category: "layout",
	edit(props) {
		return (
			<div className="block-editor-block-card">
				<InnerBlocks allowedBlocks={["howmuch/test-block"]} />
			</div>
		);
	},

	save(props) {
		return (
			<div>
				<InnerBlocks.Content />
			</div>
		);
	}
});
